import React, { useEffect, useRef, useState } from "react";
import { Badge, Button, Card, Form, Input, InputNumber, Modal, Select, Table, Tag } from "antd";

export const UpdateModal = ({ handleOk, handleCancel, modal, onDelete, onUpdate, fields, deleteId }) => {
  const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  const validateMessages = {
    required: " .",
    types: {
      number: "  ",
    },
  };
  return (
    <Modal
      title="Basic Modal"
      visible={modal}
      onOk={handleOk}
      onCancel={handleCancel}
      footer={[
        <Button
          type="danger"
          key={"delete"}
          onClick={() => {
            onDelete(deleteId);
          }}
        >
          
        </Button>,
        <Button key="back" onClick={handleCancel}>
          
        </Button>,
      ]}
    >
      <Form {...layout} fields={fields} name="addTransaction" onFinish={onUpdate} validateMessages={validateMessages}>
        <Form.Item name={"id"} hidden>
          <InputNumber disabled />
        </Form.Item>
        <Form.Item name={"title"} label=" " rules={[{ required: true }]}>
          <Input />
        </Form.Item>
        <Form.Item name={"introduction"} label=" " rules={[{ required: true }]}>
          <Input />
        </Form.Item>
        <Form.Item name={"description"} label="  " rules={[{ required: true }]}>
          <Input />
        </Form.Item>
        <Form.Item wrapperCol={{ ...layout.wrapperCol, offset: 8 }}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </Modal>
  );
};
